body {
  background-color: gray;
}
.css_input {
  height: 40px;
  width: 300px;
  margin-left: 30px;
  margin-top: 20px;
  border-radius: 20px;
  font-size: 25px;
}
.css_btn {
  height: 40px;
  width: 80px;
  font-size: 20px;
  border-radius: 20px;
  background-color: purple;
  color: whitesmoke;
}
.css_series_section {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
.css_main {
  background-color: gray;
}

.css_favourites_series {
  width: 30vw;
  display: flex;
  flex-direction: column;
}
.css_card_container {
}
.css_series_list {
  width: 70vw;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-evenly;
}
.js_serie_title {
  color: white;
  background-color: gray;
}

.css_favourites_series {
  background-color: white;
  border-radius: 20px;
  text-align: center;
  margin-left: 20px;
  margin-top: 30px;
  margin-right: 20px;
}
.css-favorites_title {
  color: gray;
  font-size: 30px;
  margin-top: 25px;
}
.css_image {
  height: 250px;
}
.js_li {
  padding: 20px;
  background-color: darkgray;
  border-radius: 25px;
  margin: 5px 5px 5px 5px;
}
.css_serie_title {
  font-size: 20px;
  background-color: darkgray;
}
.js_favorite_class {
  border: double 10px purple;
  opacity: 0.5;
  border-radius: 25px;
}
